.timer {
  background-color: #f8f8f8;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 30px;
  display: flex;
  box-shadow: 0 6px 12px #0000001a;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  color: #fff;
  text-align: center;
  background-color: #000;
  border-radius: 8px;
  padding: 20px 30px;
  font-family: Courier New, Courier, monospace;
  font-size: 4em;
  font-weight: 900;
  box-shadow: inset 0 2px 5px #fff3, 0 6px 12px #0000004d;
}

.label {
  color: #555;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 12px;
  font-size: 1.2em;
  font-weight: 700;
}
