.timer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 30px;
    background-color: #f8f8f8; 
    border-radius: 15px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  }
  
  .field {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .value {
    font-family: 'Courier New', Courier, monospace;
    font-size: 4em; 
    font-weight: 900;
    color: #fff; 
    background-color: #000; 
    padding: 20px 30px; 
    border-radius: 8px;
    box-shadow: inset 0 2px 5px rgba(255, 255, 255, 0.2), 
                0 6px 12px rgba(0, 0, 0, 0.3);
    text-align: center;
  }
  
  .label {
    font-size: 1.2em; 
    font-weight: 700; 
    color: #555;
    margin-top: 12px;
    text-transform: uppercase;
    letter-spacing: 1px;
  }